.container {
	max-width: 800px;
	margin: 0 auto;
	padding: 16px;
}

h1 {
	font-size: 3rem;
}

.is-centered {
	&,
	& > * {
		max-width: 400px;
		margin: 0 auto;
		text-align: center;
	}
}

.is-vertically-centered {
	&,
	& > * {
		display: flex;
		flex-direction: row;
		height: calc(100vh - 200px);
		align-items: center;
		justify-content: center;
	}
}

img {
	margin-top: 24px;
	width: 100%;
	height: auto;
	object-fit: contain;
}
